import { useLocation } from 'react-router-dom'
import { Logo } from './logo'

export const Footer = () => {
  const location = useLocation()
  const nav = ['  ', ' ', ' ', ' ', ' ']

  return (
    <footer className={`${location.pathname.includes('/results') ? 'full' : ''}`}>
      <div className="footer-inner">
        {location.pathname !== '/' && <Logo />}
        <nav>
          <h4></h4>
          <ul>
            {nav.map((item, idx) => (
              <li key={idx}>{item}</li>
            ))}
          </ul>
        </nav>
        <button> </button>
        <p>    2022</p>
      </div>
    </footer>
  )
}
